.infoBoard {
  padding: 40px 0;
  position: relative;
  background-color: #2c2c2c;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url("../images/content/board-bg.png");
    background-size: contain;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center bottom;
    margin: 0 auto;
  }

  &__logo {
    object-fit: contain;
  }
  .nav-tabs {
    display: flex;
    gap: 16px;
    align-items: center;
    margin-top: 32px;
    margin-bottom: 18px;
  }
  .nav-link.active {
    background-color: var(--color-red);
    border: 1px solid var(--color-red);
    color: var(--color-light);
  }
  .nav-link {
    color: var(--color-light);
    font-size: 16px;
    line-height: 24px;
    padding: 6px 16px;
    border-radius: 4px;
    border: 1px solid var(--color-light);
    cursor: pointer;
    margin: 0;
  }
}

.matchesAfter {
  margin: 0 -12px;
  li {
    padding: 0 12px;
  }
  .matchesLive__btn {
    background-color: var(--color-red);
  }
}
