.statistics-section,
.table-section,
.players-section {
  table {
    width: 100%;
    border-collapse: collapse;
  }

  .table {
    td {
      font-size: 14px;
      font-weight: 400;
      line-height: 22.4px;
      padding-top: 8px;
      padding-bottom: 8px;
    }
    th {
      text-align: left;
      padding-bottom: 8px;
      padding-top: 8px;
      font-size: 12px;
      font-weight: 400;
      line-height: 15.6px;
    }
    th,
    td {
      padding-left: 10px;
      padding-right: 10px;
      background-color: #f3f3f3;
    }
    th a,
    td a {
      text-decoration: transparent;
    }
    tr:last-child td {
      border-bottom: none;
    }
  }

  tbody tr:nth-child(odd) td {
    background: rgba(#fff, 1);
  }
  .table {
    @include media-breakpoint-down(lg) {
      min-width: 400px;
    }
  }
}
